<template>
  <div>
    <p>{{song.title}}</p>
    <img :src="song.imageUrl" height="150"/>
  </div>
</template>
<script>
export default {
  props: [
    'id'
  ],
  computed: {
    song () {
      return this.$store.getters.song(this.id)
    }
  }
}
</script>



