import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Register from '@/components/Register'
import SingIn from '@/components/SignIn'
import StoreData from '@/components/StoreData'
import ShowSongs from '@/components/ShowSongs'
import ShowSong from '@/components/ShowSong'
import AuthGuard from './auth-gaurd'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/signIn',
      name: 'signIn',
      component: SingIn
    },
    {
      path: '/storeData',
      name: 'storeData',
      component: StoreData,
      beforeEnter: AuthGuard
    },
    {
      path: '/showSongs',
      name: 'showSongs',
      component: ShowSongs
    },
    {
      path: '/showSong/:id',
      name: 'showSong',
      props: true,
      component: ShowSong
    }
  ]
})



