import * as firebase from 'firebase'

export default {
  store (data) {
    // database's driver.ref('node||table').push(data)
    const mydata = {
      title: data.title,
      artist: data.artist,
      creatorId: data.creatorId
    }
    let key
    return firebase.database().ref('songs').push(mydata)
      .then(
        res => {
          // key of the individual song generated by firebase
          key = res.key
          return key
        }
      )
      .then(
        key => {
          const fileName = data.image.name
          const ext = fileName.slice(fileName.lastIndexOf('.'))
          return firebase.storage().ref('songs/' + key + '.' + ext).put(data.image)
        }
      )
      .then(
        imageData => {
          return imageData.ref.getDownloadURL()
        }
      )
      .then(
        downloadURL => {
          firebase.database().ref('songs').child(key).update({imageUrl: downloadURL})
        }
      )
  },
  read () {
    return firebase.database().ref('songs').once('value')
  }
}



