<template>
  <v-flex xs6 offset-xs3>
    <alertbtn v-if="errMess" :message="errMess"/>
    <panel title="Register">
      <div class="pl-4 pr-4 pt-2 pb2" slot="body">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
          ></v-text-field>
          <v-text-field
            v-model="password"
            :rules="passwordRules"
            label="password"
            type="password"
            required
          ></v-text-field>
          <v-btn
            :disabled="!valid"
            @click="register"
            class="myBtnColor amber darken-2"
          >
            register
          </v-btn>
        </v-form>
      </div>
    </panel>
  </v-flex>
</template>
<script>
/**
 * Use to bind the html to the controller
 * Everything insid the script tag is the controller
 */
import Panel from '@/components/Panel'
import Alertbtn from '@/components/Alertbtn'
import AuthenticationController from '../controller/AuthenticationController'
export default {
  data: () => ({
    errMess: null,
    valid: true,
    email: '',
    emailRules: [
      v => !!v || 'E-mail is required',
      v => /.+@.+/.test(v) || 'E-mail must be valid'
    ],
    password: '',
    passwordRules: [
      v => !!v || 'Password is required'
    ]
  }),
  methods: {
    async register () {
      if (this.$refs.form.validate()) {
        this.$store.dispatch('clErr')
        await AuthenticationController.register({
          email: this.email,
          password: this.password
        }).then(
          res => {
            this.$store.dispatch('signUpUser', res.user)
          }
        ).catch(
          err => {
            this.$store.dispatch('err', err.message)
            this.error = true
            this.errMess = this.$store.getters.error
          }
        )
      } else {
        console.log('Invalid form')
      }
    }
  },
  computed: {
    user () {
      return this.$store.getters.userId
    }
  },
  watch: {
    user (userId) {
      if (userId !== null && userId !== undefined) {
        this.$router.push('/')
      }
    }
  },
  components: {
    Panel,
    Alertbtn
  }
}
</script>
<style>
</style>



