<template>
  <v-layout row wrap>
    <v-flex>
      <v-progress-circular
      indeterminate
      color="amber darken-2"
      v-if="loading"
      ></v-progress-circular>
      <div v-for="song in songs" :key="song.id">
        <card :title="song.title" :artist="song.artist" :src="song.imageUrl" :link="'/showSong/'+song.id"/>
        <br/>
      </div>
    </v-flex>
  </v-layout>
</template>
<script>
import SongController from '@/controller/SongController'
import Panel from '@/components/Panel'
import Card from '@/components/Card'
export default {
  data () {
    return {
      loading: true,
      songs: []
    }
  },
  async mounted () {
    await SongController.read().then(
      res => {
        this.$store.dispatch('addSong', res)
        this.songs = this.$store.getters.songs
        if (this.songs) { this.loading = false }
      }
    ).catch(
      err => {
        console.log(err)
      }
    )
  },
  components: {
    Panel,
    Card
  }
}
</script>



