<template>
  <div>
    <v-navigation-drawer
      v-model="drawer"
      clipped
      fixed
      app
      light
      temporary
    >
      <v-list dense>
        <v-list-tile class="myCursor">
          <v-list-tile-action>
            <v-icon>dashboard</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Dashboard</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile class="myCursor">
          <v-list-tile-action>
            <v-icon>settings</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>Settings</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar app fixed clipped-left dark class="amber darken-2">
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>Application</v-toolbar-title>
      <v-spacer/>
      <v-toolbar-items v-for="item in menuItems" :key="item.title">
        <v-btn flat :to="item.link" class="navBar-link">
          <v-icon left>{{item.icon}}</v-icon>
          {{item.title}}
        </v-btn>
      </v-toolbar-items>
      <v-toolbar-items v-if="this.userIsAuthenticated">
        <v-btn flat class-="navBar-link" @click="logOut">
          <v-icon left>exit_to_app</v-icon>
          Sign out
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
  </div>
</template>
<script>
import AuthenticationController from '../controller/AuthenticationController'
export default {
  data: () => ({
    drawer: false
  }),
  methods: {
    async logOut () {
      await AuthenticationController.signOut().then(
        this.$store.dispatch('logOut')
      ).catch(
        err => {
          console.log(err)
        }
      )
    }
  },
  computed: {
    menuItems () {
      var menuItems = [
        {icon: 'face', title: 'Sign up', link: '/register'},
        {icon: 'lock_open', title: 'Sign in', link: '/signIn'}
      ]
      if (this.userIsAuthenticated) {
        menuItems = [
          {icon: 'music_video', title: 'Songs', link: '/showSongs'},
          {icon: 'add', title: 'add song', link: '/storeData'}
        ]
      }
      return menuItems
    },
    userIsAuthenticated () {
      return this.$store.getters.userId !== null && this.$store.getters.userId !== undefined
    }
  }
}
</script>
<style scoped>
.myCursor {
cursor: pointer;
}
</style>



