import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    userId: null,
    err: null,
    songs: []
  },
  mutations: {
    setUserId (state, userId) {
      state.userId = userId
    },
    setSong (state, songs) {
      const obj = songs.val()
      let newSongs = []
      for (let key in obj) {
        newSongs.push({
          id: key,
          title: obj[key].title,
          artist: obj[key].artist,
          creatorId: obj[key].creatorId,
          imageUrl: obj[key].imageUrl
        })
      }
      state.songs = newSongs
    },
    setErr (state, err) {
      state.err = err
    },
    clearErr (state) {
      state.err = null
    }
  },
  actions: {
    signUpUser ({commit}, User) {
      commit('setUserId', User.uid)
    },
    signInUser ({commit}, User) {
      commit('setUserId', User.uid)
    },
    addSong ({commit}, Songs) {
      commit('setSong', Songs)
    },
    err ({commit}, err) {
      commit('setErr', err)
    },
    clErr ({commit}) {
      commit('clearErr')
    },
    autoSignIn ({commit}, User) {
      commit('setUserId', User.uid)
    },
    logOut ({commit}) {
      commit('setUserId', null)
    }
  },
  getters: {
    userId (state) {
      return state.userId
    },
    error (state) {
      return state.err
    },
    songs (state) {
      return state.songs
    },
    song (state) {
      return (songId) => {
        return state.songs.find((song) => {
          return song.id === songId
        })
      }
    }
  }
})



