import Vue from 'vue'
import App from './App'
import router from './router'
import * as firebase from 'firebase'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import {sync} from 'vuex-router-sync'
import store from '@/store/store'
Vue.config.productionTip = false

Vue.use(Vuetify)

sync(store, router)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyBKgU5JS2_9qPffHkicU79CVLzYpzdQXgs',
      authDomain: 'vue-firebase-86ad4.firebaseapp.com',
      databaseURL: 'https://vue-firebase-86ad4.firebaseio.com',
      projectId: 'vue-firebase-86ad4',
      storageBucket: 'gs://vue-firebase-86ad4.appspot.com'
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
      }
    })
  }
})



