<template>
  <v-flex xs6 offset-xs3>
    <panel title="Songs">
      <div class="pl-4 pr-4 pt-2 pb2" slot="body">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            v-model="title"
            :rules="titleRules"
            label="Title"
            required
          ></v-text-field>
          <v-text-field
            v-model="artist"
            :rules="artistRules"
            label="Artist"
            required
          ></v-text-field>
          <div>
            <v-btn
            @click="onPickFile"
            class="amber darken-2 mycolor"
            outline
            >
            choose image
            </v-btn>
            <img :src="imageUrl" height="150"/>
            <input
            type="file"
            class="hideme"
            ref="fileInput"
            accept="image/*"
            required
            @change="onFilePicked"/>
          </div>
          <v-btn
            :disabled="!valid"
            @click="store"
            class="myBtnColor amber darken-2"
          >
            Add
          </v-btn>
        </v-form>
      </div>
    </panel>
  </v-flex>
</template>
<script>
import Panel from '@/components/Panel'
import SongController from '../controller/SongController'
export default {
  data: () => ({
    image: null,
    imageUrl: '',
    valid: true,
    title: '',
    titleRules: [
      v => !!v || 'Title is required'
    ],
    artist: '',
    artistRules: [
      v => !!v || 'Artist is required'
    ]
  }),
  methods: {
    async store () {
      if (this.$refs.form.validate()) {
        await SongController.store({
          title: this.title,
          artist: this.artist,
          image: this.image,
          creatorId: this.$store.getters.userId
        }).then(
          res => {
            console.log(res)
          }
        ).catch(
          err => {
            console.log(err)
          }
        )
      }
    },
    onPickFile () {
      this.$refs.fileInput.click()
    },
    onFilePicked (event) {
      const image = event.target.files
      let imageName = image[0].name
      if (imageName.lastIndexOf('.') <= 0) {
        return alert('Please add a valid image')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.imageUrl = fileReader.result
      })
      fileReader.readAsDataURL(image[0])
      this.image = image[0]
    }
  },
  components: {
    Panel
  }
}
</script>
<style>
  .hideme {
    display: none
  }
  .mycolor {
    color: #FFA000 !important
  }
</style>



